<template>
  <div class="goods-info" v-if="Object.keys(detailInfo).length !== 0">
    <div class="info-desc">
      <div class="start"></div>
      <div class="desc">
        {{detailInfo.desc}}
      </div>
      <div class="end"></div>
    </div>
    <div class="info-key">{{detailInfo.detailImage[0].key}}</div>
    <div class="info-list" >
      <img v-for="(item,index) in detailInfo.detailImage[0].list"
           :key="index"
           :src="item" alt=""
           @load="imageLoad">
    </div>
  </div>
</template>

<script>
    export default {
        name: "DetailGoodsInfo",
        props:{
            detailInfo:{
                type:Object,
                default(){
                    return {}
                }
            }
        },
        data(){
            return {
                counter: 0,
                imageLength: 0
            }
        },
        methods:{
            imageLoad(){
                if(++this.counter === this.imageLength) {
                    this.$emit('imageLoad')
                }
            }
        },
        watch:{
            detailInfo() {
                this.imageLength = this.detailInfo.detailImage[0].list.length
            }
        }
    }
</script>

<style scoped>
  .goods-info{
    padding: 20px 0;
    border-bottom: 5px solid #f2f5f8;
  }
.info-desc{
  padding: 0 15px;

}
.info-desc .desc{
  padding: 15px 0;
}
.info-desc .start , .info-desc .end{
  width: 90px;
  height: 1px;
  background-color: #a3a3a5;
  position: relative;
}
.info-desc .start{
  float: left;
}
.info-desc .end{
  float: right;
}
  .info-desc .start::before , .info-desc .end::after{
    content: '';
    width: 5px;
    height: 5px;
    background-color: #333333;
    position: absolute;
    bottom: 0;
  }
  .info-key{
    margin: 10px 0 10px 15px;
    color: #333;
    font-size: 15px;
  }
  .info-list img{
    width: 100%;
  }
</style>
